body {
    font-family: sans-serif,Verdana,Tahoma, sans-serif;
}

:root {
    --main-color:#e94560;
    --second-color:#f7ce26;
}

li {
    list-style: none;
}
a {
    text-decoration: none;
}



/* ===== Container Product Page ====== */
.container-product-page {
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
}
.container-product-page .single-product {
    flex: 2;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0px 0px 3px 0px #aaa;
    border-radius: 5px;
}
.container-product-page .single-product .product-img {
    flex: 1;
    padding: 15px;
    text-align: center;
}
.container-product-page .single-product .product-img img {
    border-radius: 5px;
    width: 400px;
    height: 300px;
    object-fit: cover;
}
.container-product-page .single-product .product-info {
    flex: 1;
    padding: 15px;
}
.container-product-page .single-product .product-info .name {
    text-wrap: balance;
    line-height: 28px;
    color: #333;
    font-size: 20px;
    padding: 10px;
}
.container-product-page .single-product .product-info .stars {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 15px 5px;
}
.container-product-page  .product-info .stars li {
    padding: 5px;
    color: #dd4b39;
}
.container-product-page  .product-info .stars li span {
    color: #aaa;
}
.container-product-page  .product-info .price {
    padding: 5px;
    color: #555;
    font-weight: 900;
}
.container-product-page  .product-info .seller {
    color: #222;
    font-size: 17px;
}
.container-product-page  .product-info .description {
    padding: 10px;
    line-height: 1.6;
    text-wrap: balance;
    width: 200px;
}
.container-product-page .product-property form {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}
.container-product-page .product-property select {
    margin: 10px 0;
}
.container-product-page .review {
    flex: 1;
}

/* =========== Review Section ============ */

.container-review {
    padding: 10px;
}

.container-review h2 {
    background-color: #dd4b39;
    width: 125px;
    padding: 10px;
    color: #fff;
    border-radius: 5px;
    font-size: 20px;
}
.container-review .content {
    display: flex;
    flex-direction: column;

}
.container-review .content .review {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;
}
.container-review .review .instructor-img {
    padding: 10px 20px;
}
.container-review .review .instructor-img img {
    width: 100px;
}
.container-review .review .info {
    flex: 1;
}
.container-review .review .info .data {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 0;
}
.container-review .review .info h3 {
    padding: 0 10px;
    font-size: 20px;
}
.container-review .review .info h4 {
    padding: 10px;
    color: #444;
}
.container-review .review .info ul {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px;
}
.container-review .review .info ul li {
    color: #dd4b39;
    padding: 0 5px;
}
.container-review .review .info .message {
    line-height: 1.6;
    color: #888;
}








/* =======  Popular Post =========== */
.container-product-page .shop-right-side {
    flex: 1;
}
.container-product-page .shop-right-side .container-search {
    display: none;
}




@media (max-width:500px) {
    .container-product-page .single-product .product-img img {
        width: 100px;
    }
}